/* global SD, GeoPoint, SDGeocode, map */
import * as React from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { makeStyles } from '@mui/styles';
import { useState } from 'react'
import { NumberChip as Chip } from '../components/Chip';
import Button from '@mui/material/Button';
import Menu from '../components/Menu'
import Slider from '@mui/material/Slider';
import DoodleBoard from '../components/canvas/Doodleboard'
import { Stack } from '@mui/material'
import { East, ZoomIn, ZoomOut, ClearAll, North, South, West } from '@mui/icons-material/';
import { navigate } from "gatsby"
import { useTheme } from "@mui/material";
import { styled } from '@mui/material/styles';
import Layout from "../components/layout";

const { useEffect, useRef } = React;

const Container = styled(Box)(({ theme }) => ({
    alignItems: "center",
    justifyContent: "center",
    display: 'flex',
    flexDirection: 'column',
    [theme.breakpoints.up("lg")]: {
        flexDirection: 'column',
    }
}));

const MapLayout = styled(Box)(({ theme }) => ({
    '@media (min-width: 0px)': {
        display: 'none'
    },
    '@media (min-width: 1200px)': {
        display: 'block'
    }
}));

const NotSupported = styled(Box)(({ theme }) => ({
    '@media (min-width: 0px)': {
        display: 'block'
    },
    '@media (min-width: 1200px)': {
        display: 'none'
    }
}));

const LENGTH = 1200
const geoUnit = 0.00001


function parseQuery(queryString) {
    var query = {};
    var pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
    for (var i = 0; i < pairs.length; i++) {
        var pair = pairs[i].split('=');
        query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || '');
    }
    return query;
}

const drawDotAtCenter = (canvas) => {
    const ctx = canvas.getContext('2d');

    // Calculate the center of the canvas
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;

    // Draw a dot at the center
    ctx.beginPath();
    ctx.arc(centerX, centerY, 5, 0, 2 * Math.PI); // 5 is the radius of the dot
    ctx.fillStyle = 'red';
    ctx.fill();
}

const drawDirections = (canvas) => {

    const ctx = canvas.getContext('2d');
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;
    const radius = Math.min(centerX, centerY);
    console.log(centerX)
    ctx.clearRect(0, 0, canvas.width, canvas.height);


    // Draw the circle
    ctx.beginPath();
    ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
    ctx.stroke();

    // Directions in degrees
    const directions = [
        { angle: 0, label: '' }, // North
        { angle: 45, label: '' }, // North-East
        { angle: 90, label: '' }, // East
        { angle: 135, label: '' }, // South-East
        { angle: 180, label: '' }, // South
        { angle: 225, label: '' }, // South-West
        { angle: 270, label: '' }, // West
        { angle: 315, label: '' } // North-West
    ];

    // Convert degrees to radians
    directions.forEach(direction => {
        const angleInRadians = (direction.angle + 22.5) * (Math.PI / 180);
        const x = centerX + radius * Math.cos(angleInRadians);
        const y = centerY + radius * Math.sin(angleInRadians);

        // Draw line
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);
        ctx.lineTo(x, y);
        ctx.stroke();

        // Draw label
        ctx.fillText(direction.label, x, y);
    });
    drawDotAtCenter(canvas)
}


var winOject = (typeof window !== 'undefined') ? window : {}


const IndexPage = ({ data, location }) => {
    const theme = useTheme()
    console.log(theme)

    const canvasRef = useRef(null)
    const dotRef = useRef(null)
    const divRef = useRef(null)
    const [postal, setPostal] = useState("")
    const [geocode, setGeocode] = useState({
        lat: 103.80733282853,
        lng: 1.289478890541
    })
    const [zoom, setZoom] = useState(13)
    const [boardKey, setBoardKey] = useState(0);
    const params = parseQuery(location.search)
    const hideLogo = params.hideLogo;
    const [drag, setDrag] = useState(false)

    const findGeo = (q) => {
        var geocode = new SDGeocode(winOject.map);
        var searchOption = { "q": q };
        var gc = SDGeocode.SG;
        geocode.requestData(gc, searchOption)
    }

    const moveMap = (horizontal, vertical) => {
        setGeocode(e => {
            return {
                lat: e.lat + vertical,
                lng: e.lng + horizontal
            }
        })
    }


    const loadMap = () => {

        winOject.set_data = (json) => {

            if (json[0]) {
                setGeocode({
                    lat: json[0].x,
                    lng: json[0].y,
                })
            }
        }
        const latlng = new GeoPoint(geocode.lat, geocode.lng);
        const myOptions = {
            zoom: zoom,
            center: latlng,
            showCopyright: false
        };
        winOject.map = new SD.genmap.Map(
            divRef.current,
            myOptions
        );

        drawDirections(canvasRef.current)
    }

    useEffect(() => {
        loadMap()
    }, [])

    useEffect(() => {
        var latlng = new GeoPoint(geocode.lat, geocode.lng);
        winOject?.map.setCenter(latlng, zoom)
    }, [geocode, zoom])


    useEffect(() => {
        map.setZoom(zoom, true)
    }, [zoom])

    const clearCanvas = (canvas) => {
        setBoardKey(boardKey + 1)
    };

    useEffect(() => {
        if (!drag) {
            drawDirections(canvasRef.current)
        } else {
            drawDotAtCenter(dotRef.current)
        }
    }, [drag])

    return (
        <Layout fullWidth={true}>
            <Container>
                {
                    !hideLogo && <img alt="" onClick={() => navigate('/')} src="/images/logo2.png" style={{ width: "256px", cursor: 'pointer' }} />
                }
                <MapLayout>
                    <Box display="flex" flexDirection="row" mb="16px">
                        <TextField id="outlined-basic" label="Postal Code" variant="outlined"
                            onChange={e => setPostal(e.target.value)}
                            value={postal}
                        />
                        <Button
                            size="small"
                            onClick={() => findGeo(postal)}
                        >Find</Button>

                    </Box>
                    <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
                        <ZoomOut />
                        <Slider
                            valueLabelDisplay="on"
                            value={zoom}
                            step={1}
                            min={1}
                            max={15}
                            onChange={(e, v) => setZoom(v)} />


                        <ZoomIn />



                        <Button
                            variant="contained"
                            size="small"
                            startIcon={<North />}
                            onClick={() => moveMap(geoUnit, 0)}
                        >N</Button>

                        <Button
                            variant="contained"
                            size="small"
                            startIcon={<South />}
                            onClick={() => moveMap(geoUnit * -1, 0)}
                        >S</Button>

                        <Button
                            variant="contained"
                            size="small"
                            startIcon={<West />}
                            onClick={() => moveMap(0, geoUnit * -1)}
                        >E</Button>

                        <Button
                            variant="contained"
                            size="small"
                            startIcon={<East />}
                            onClick={() => moveMap(0, geoUnit)}
                        >W</Button>

                        <Button
                            size="small"
                            variant="contained"
                            onClick={() => clearCanvas(divRef.current)}
                        >Clean</Button>

                        <Button
                            variant="contained"
                            size="small"
                            onClick={() => setDrag(!drag)}
                        >Drag</Button>
                    </Stack>


                    <Box style={{ position: 'relative', width: `${LENGTH}px`, height: `${LENGTH}px`, }}>
                        <div id="map_canvas" ref={divRef} style={{ width: `100%`, height: `100%` }}>
                            {
                                <Box display={drag ? 'none' : 'block'}>
                                    <canvas id="pie" ref={canvasRef} width={LENGTH} height={LENGTH} style={{ cursor: 'url(images/pencil-cursor.png),default', position: 'absolute', zIndex: 1000000 }}></canvas>
                                    <DoodleBoard
                                        key={boardKey}
                                        zIndex={10000001}
                                        height={LENGTH}
                                        width={LENGTH}
                                    />
                                </Box>
                            }
                            <canvas id="dot" width={20} height={20} style={{
                                zIndex: 1000000, position: 'absolute',
                                left: 0,
                                right: 0,
                                top: 0,
                                bottom: 0,
                                marginTop: 'auto',
                                marginBottom: 'auto',
                                marginLeft: 'auto',
                                marginRight: 'auto',

                            }} ref={dotRef}></canvas>
                        </div>
                    </Box>
                </MapLayout>
                <NotSupported>
                    <h2> This feature is only available using desktop. You can try our other features.</h2>
                    <Menu />
                </NotSupported>
            </Container ></Layout>)
}

export default IndexPage
